export const login = async (values) => {
  try {
    const { data } = await api.post("/auth/login", {
      name: values.name,
      email: values.email,
      password: values.password,
    });

    // üîë —Ç–æ–∫–µ–Ω –∑–±–µ—Ä—ñ–≥–∞—î–º–æ —É localStorage
    if (data.token) {
      localStorage.setItem("token", data.token);
    }

    return data;
  } catch (error) {
    console.error("Login failed:", error.response?.data || error.message);
    throw error;
  }
};
//LogOut
export const logout = async (token) => {
  await api.post(
    "/auth/logout",
    {}, // —Ç—ñ–ª–æ –ø—É—Å—Ç–µ
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  localStorage.removeItem("token"); // ‚ö° –ø—Ä–∞–≤–∏–ª—å–Ω–æ –≤–∏–¥–∞–ª—è—î–º–æ
};

// GET –≤—Å—ñ –±–ª–æ–≥–∏
export const getBlogs = async () => {
  const { data } = await api.get("/blog");
  return data;
};

// GET –æ–¥–∏–Ω –±–ª–æ–≥
export const getBlog = async (id) => {
  const { data } = await api.get(`/blog/${id}`);
  return data;
};

// POST –Ω–æ–≤–∏–π –±–ª–æ–≥
export const createBlog = async ({ title, content }, token) => {
  console.log(title, content);
  const res = await api.post(
    "/blog",
    { title, content }, // ‚úÖ –ø—Ä–∞–≤–∏–ª—å–Ω–∞ —Å—Ç—Ä—É–∫—Ç—É—Ä–∞
    {
      headers: { Authorization: `Bearer ${token}` },
    }
  );
  console.log(res.data);
  return res.data;
};

// PUT –æ–Ω–æ–≤–ª–µ–Ω–Ω—è –±–ª–æ–≥—É
export const updateBlog = async ({ id, data, token }) => {
  const res = await api.put(`/blog/${id}`, data, {
    headers: { Authorization: `Bearer ${token}` },
  });
  return res.data;
};

// DELETE –±–ª–æ–≥
export const deleteBlog = async (id, token) => {
  const res = await api.delete(`/blog/${id}`, {
    headers: { Authorization: `Bearer ${token}` },
  });
  return res.data;
};
